import React from "react";
import { Shop } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const StoreDash = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="col-6 col-md-4 ">
        {" "}
        <div className="dashboard mb-2">
          <Dropdown style={{ width: "100%" }}>
            <Dropdown.Toggle
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              variant="transparent"
            >
              <div>
                <div className="d-flex justify-content-center">
                  <Shop className="dash-icon" />
                </div>
                <p className="text-center dash-text">Store/Inventory</p>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`store-categories`);
                  }}
                >
                  Store Categories
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`store-items`);
                  }}
                >
                  Store Items
                </p>
              </Dropdown.Item>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`store-entries`);
                  }}
                >
                  Store Entries
                </p>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
    </>
  );
};

export default StoreDash;
