import React from "react";
import {
  Award,
  BarChartSteps,
  BoundingBox,
  Calendar4Range,
  CashStack,
  ColumnsGap,
  FolderCheck,
  ListCheck,
  PersonBadge,
} from "react-bootstrap-icons";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
const AdminDash = () => {
  const navigate = useNavigate();
  const inst = useSelector((state) => state.inst.activeInst);
  return (
    <>
      <div className="col-6 col-md-4 ">
        <div
          className="dashboard"
          onClick={() => {
            navigate(`structure/${"academic-year"}`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Calendar4Range className="dash-icon" />
          </div>
          <p className="text-center dash-text">Academic Years</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`structure/${"level"}`);
          }}
        >
          <div className="d-flex justify-content-center">
            <BarChartSteps className="dash-icon" />
          </div>
          <p className="text-center dash-text">Levels / Classes</p>
        </div>
      </div>
      {parseInt(inst.higher) === 1 ? (
        <div className="col-6 col-md-4 ">
          {" "}
          <div
            className="dashboard"
            onClick={() => {
              navigate(`structure/${"faculty"}`);
            }}
          >
            <div className="d-flex justify-content-center">
              <BoundingBox className="dash-icon" />
            </div>
            <p className="text-center dash-text">Faculties</p>
          </div>
        </div>
      ) : null}
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`structure/${"period"}`);
          }}
        >
          <div className="d-flex justify-content-center">
            <ColumnsGap className="dash-icon" />
          </div>
          <p className="text-center dash-text">Periods / Semesters / Terms</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`subjects`);
          }}
        >
          <div className="d-flex justify-content-center">
            <FolderCheck className="dash-icon" />
          </div>
          <p className="text-center dash-text">Subjects / Units</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`activities`);
          }}
        >
          <div className="d-flex justify-content-center">
            <ListCheck className="dash-icon" />
          </div>
          <p className="text-center dash-text">Subjects / Units Assessments</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`grading`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Award className="dash-icon" />
          </div>
          <p className="text-center dash-text">Grading</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`staffs`);
          }}
        >
          <div className="d-flex justify-content-center">
            <PersonBadge className="dash-icon" />
          </div>
          <p className="text-center dash-text">Staffs</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`modes`);
          }}
        >
          <div className="d-flex justify-content-center">
            <CashStack className="dash-icon" />
          </div>
          <p className="text-center dash-text">Payment Modes</p>
        </div>
      </div>
    </>
  );
};

export default AdminDash;
