import React from "react";
import { Cash, CashCoin, Receipt } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const FinanceDash = () => {
  const navigate = useNavigate();
  return (
    <>
      <div className="col-6 col-md-4 ">
        <div
          className="dashboard"
          onClick={() => {
            navigate(`invoices`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Receipt className="dash-icon" />
          </div>
          <p className="text-center dash-text">Invoices</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`payments`);
          }}
        >
          <div className="d-flex justify-content-center">
            <Cash className="dash-icon" />
          </div>
          <p className="text-center dash-text">Payments</p>
        </div>
      </div>
      <div className="col-6 col-md-4 ">
        {" "}
        <div className="dashboard mb-2">
          <Dropdown style={{ width: "100%" }}>
            <Dropdown.Toggle
              style={{
                width: "100%",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
              variant="transparent"
            >
              <div>
                <div className="d-flex justify-content-center">
                  <CashCoin className="dash-icon" />
                </div>
                <p className="text-center dash-text">Expenses</p>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu>
              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`expensecats`);
                  }}
                >
                  Expense Categories
                </p>
              </Dropdown.Item>

              <Dropdown.Item>
                <p
                  className="btn-link"
                  onClick={() => {
                    navigate(`expenses`);
                  }}
                >
                  Expenses
                </p>
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
    </>
  );
};

export default FinanceDash;
