import React, { useEffect } from "react";
import AcademicsDash from "../Academics/AcademicsDash";
import { useDispatch, useSelector } from "react-redux";
import AdminDash from "../Admin/AdminDash";
import FinanceDash from "../Finance/FinanceDash";
import LibraryDash from "../Library/LibraryDash";
import { People, ClockHistory } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import ClientDash from "../Client/ClientDash";
import StoreDash from "../Store/StoreDash";
import { FetchLiveSubs } from "../../Network/SubApi";
import { VerifySub } from "../../Utils/VerifySub";

const HomeDash = () => {
  //redux dispatch
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    FetchLiveSubs(User.token, ActiveCred.instLinker, 0, dispatch);
  }, []);

  const User = useSelector((state) => state.auth.user);
  const ActiveCred = useSelector((state) => state.cred.activeCred);
  const subs = useSelector((state) => state.sub.subs);
  const Sub = VerifySub(subs, ActiveCred.instLinker);

  return Sub > 0 ? (
    <div className="row m-3 d-flex justify-content-around">
      {parseInt(ActiveCred.admin) === 1 ? <AdminDash></AdminDash> : null}
      {parseInt(ActiveCred.admin) === 1 ||
      parseInt(ActiveCred.finance) === 1 ? (
        <FinanceDash></FinanceDash>
      ) : null}
      {parseInt(ActiveCred.admin) === 1 ||
      parseInt(ActiveCred.finance) === 1 ||
      parseInt(ActiveCred.library) === 1 ||
      parseInt(ActiveCred.tutor) === 1 ? (
        <>
          <div className="col-6 col-md-4 ">
            <div
              className="dashboard"
              onClick={() => {
                navigate(`students`);
              }}
            >
              <div className="d-flex justify-content-center">
                <People className="dash-icon" />
              </div>
              <p className="text-center dash-text">Students</p>
            </div>
          </div>
          <AcademicsDash></AcademicsDash>
          <LibraryDash></LibraryDash>
          <StoreDash></StoreDash>
        </>
      ) : null}
      {parseInt(ActiveCred.student) === 1 ||
      parseInt(ActiveCred.guardian) === 1 ? (
        <ClientDash></ClientDash>
      ) : null}
      <div className="col-6 col-md-4 ">
        {" "}
        <div
          className="dashboard"
          onClick={() => {
            navigate(`timetable`);
          }}
        >
          <div className="d-flex justify-content-center">
            <ClockHistory className="dash-icon" />
          </div>
          <p className="text-center dash-text">TimeTable</p>
        </div>
      </div>
    </div>
  ) : (
    <></>
  );
};

export default HomeDash;
